import React from "react";
import { Link } from "react-router-dom";
import login from "../img/login.svg";
import person from "../img/person.svg";
import path from "../img/path.svg";
import fb from "../img/facebookG.svg";
import line from "../img/lineG.svg";
import ig from "../img/instagramG.svg";
import TopBtn from "../components/TopBtn";

const Header = () => {
  const setActive = (e) => {
    let nav = document.querySelectorAll("header a");
    nav.forEach((a) => {
      a.classList.remove("active");
    });
    let target = e.target;
    target.classList.add("active");
    document.querySelector("header").classList.add("white");
  };
  const clearActive = () => {
    let nav = document.querySelectorAll("header a");
    nav.forEach((a) => {
      a.classList.remove("active");
    });
    document.querySelector("header").classList.remove("white");
  };

  return (
    <header>
      <Link to="/" className="logo" onClick={clearActive}>
        <h1>人晨</h1>
      </Link>

      <ul onClick={setActive}>
        <li>
          <Link to="/about">關於人晨</Link>
        </li>
        <li>
          <Link to="/a">教育課程</Link>
        </li>
        <li>
          <Link to="/b">商品資訊</Link>
        </li>
        <li>
          <Link to="/c">場地租借</Link>
        </li>
        <li>
          <Link to="/d">合作店家</Link>
        </li>
        <li>
          <Link to="/e">聯絡我們</Link>
        </li>
      </ul>
      <div className="container">
        <div className="container1">
          <div className="loginReg">
            <Link to="/login" className="login">
              <img src={login} alt="login" />
              登入
            </Link>
            <span>/</span>
            <Link to="/regist" className="regist">
              註冊
            </Link>
          </div>
          <Link to="/user" className="user">
            <img src={person} alt="person" />
            會員中心
          </Link>
          <div className="fanPages">
            <Link to="/facebook">
              <img src={fb} alt="fb" />
            </Link>
            <Link to="/line">
              <img src={line} alt="line" />
            </Link>
            <Link to="/instagram">
              <img src={ig} alt="ig" />
            </Link>
          </div>
        </div>
        <div className="search">
          <img src={path} alt="path" />
          <input type="text" />
          <button>搜尋</button>
        </div>
      </div>
      <TopBtn />
    </header>
  );
};

export default Header;
